
import VHeader from '@/components/v-header/v-header'
import imageTop from '@/assets/img/default_avatar.png'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from '@/api/axiosApi'
import { QINIU_PREFIX } from '@/api/config'

export default {
data () {
return {
imageTop: imageTop,
styles: {
height: '0px'
},
fansList: []
}
},
methods: {
handleClickFans (follower) {
this.$router.push('/meiTu/userDynamic/' + follower)
}
},
created () {
this.cellContentWith = window.innerWidth - 55 - 95

axios.get(this, '/v1/follows', {following: this.$route.params.userId, valid: 1}, (data) => {
data.forEach(item => {
if (item.followerHeadUrl) {
if (item.followerHeadUrl.indexOf(QINIU_PREFIX) > -1) {
item.followerHeadUrl += '-bheader'
}
} else {
item.followerHeadUrl = imageTop
}
})
this.fansList = data
})
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
},
components: {
VHeader,
Scroller,
Cell,
CellBox,
imageTop,
XInput,
Calendar,
XTextarea,
XButton,
Group
}
}



